.app {
  text-align: center;
  font-size: 1.5em;
}

.name {
  color: black;
  font-family: "Titillium Web", sans-serif;
  margin: 0px auto 0px auto;
}

.title {
  color: black;
  font-family: "Titillium Web", sans-serif;
  margin-top: 0px;
  font-size: 2.5em;
  letter-spacing: 2px;
}

.hr {
  border: 0;
  height: 1px;
  background-image: -webkit-gradient(
    linear,
    left top, right top,
    from(rgba(0, 0, 0, 0)),
    color-stop(rgba(0, 0, 0, 0.75)),
    to(rgba(0, 0, 0, 0))
  );
  background-image: -webkit-linear-gradient(
    left,
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 0.75),
    rgba(0, 0, 0, 0)
  );
  background-image: -o-linear-gradient(
    left,
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 0.75),
    rgba(0, 0, 0, 0)
  );
  background-image: linear-gradient(
    to right,
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 0.75),
    rgba(0, 0, 0, 0)
  );
  width: 60%;
}



